.text-on-image-culture {
	position: absolute;
	color: #fff;
	text-decoration: none;
	font-size: 2em;
	font-weight: bolder;
	width: 100%;
	top: 25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.face-container1,
.face-container2,
.face-container3,
.M6-company {
	width: 100%;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	margin-bottom: 6%;
	position: relative;
}

.face-container4 {
	width: 100%;
	display: -ms-inline-flexbox;
	display: inline-flex;
	-ms-flex-direction: row;
	    flex-direction: row;
	-ms-flex-pack: center;
	    justify-content: center;
	margin-bottom: 6%;
	position: relative;
}

.header-culture {
	font-size: 1em;
}

.face-wrap {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 16%;
	height: 35vh;
}

.face,
.bulletin {
	border-radius: 10%;
}

.image-hover {
	position: absolute;
	height: 100%;
	width: 35vh;
	opacity: 0;
	-webkit-transition: 0.5s ease;
	-o-transition: 0.5s ease;
	transition: 0.5s ease;
	background-color: white;
	border-radius: 10%;
}

.image-hover-1 {
	position: absolute;
	height: 100%;
	width: 36vh;
	opacity: 0;
	-webkit-transition: 0.5s ease;
	-o-transition: 0.5s ease;
	transition: 0.5s ease;
	background-color: white;
	border-radius: 10%;
}

.text-on-hover {
	color: black;
	font-size: 22px;
	position: absolute;
	font-weight: bolder;
	top: 50%;
	left: 50%;
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
	text-align: center;
}

.face-wrap:hover .image-hover {
	opacity: 0.7;
}

.face-wrap:hover .image-hover-1 {
	opacity: 0.7;
}

.bulletin:hover {
	-webkit-transition: all 0.2s ease-in;
	-o-transition: all 0.2s ease-in;
	transition: all 0.2s ease-in;
	-webkit-transform: scale(1.1);
	    -ms-transform: scale(1.1);
	        transform: scale(1.1);
}

@media screen and (max-width: 1000px) {
	/*화면 줄어들었을때 컬쳐 페이지 얼굴과 사진들*/
	.face,
	.bulletin {
		width: 50vh;
		height: 100%;
	}

	.face-container {
		width: 100%;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: row;
		    flex-direction: row;
		-ms-flex-wrap: wrap;
		    flex-wrap: wrap;
		-ms-flex-pack: distribute;
		    justify-content: space-around;
		margin-bottom: 6%;
	}
}

@media screen and (max-width: 960px) {
	.face-container1,
	.face-container2,
	.face-container3,
	.M6-company {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-direction: column;
		    flex-direction: column;
		-ms-flex-pack: justify;
		    justify-content: space-between;
		-ms-flex-align: center;
		    align-items: center;
		margin-bottom: 0;
	}

	.face-wrap {
		margin-bottom: 4%;
	}

	.image-hover,
	.image-hover-1 {
		height: 35vh;
	}
}

@media screen and (max-width: 470px) {
	.face-container1 {
		margin-top: 30%;
	}
}



